import React from 'react'

const Numbers = ({persons, deletePerson, filter}) => {
    const filteredPersons = persons.reduce((result, person) => {
        if(new RegExp('^.*' + filter + '.*$', 'i').test(person.nimi)) {
            result.push(person)
        }
        return result
    }, [])
   
    return filteredPersons.map((p, index) => 
        <div key={index}>
            {`${p.nimi} ${p.numero} `}
            <button onClick={() => deletePerson(p.id,p.nimi)}>poista</button>
        </div>
        )
}

export default Numbers